.google-photo-pages {
    &__main-php {
        &__text {
            color: white;
            text-align: center;
        }
    }
    &__file {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 0 auto;
        padding: 1rem;
        width: 320px;
        max-height: 1704px;
        border: 1px solid var(--grey-color);
        border-radius: 4px;
        background-color: var(--notmain-color);
        margin-top: 1rem;
    }
}